.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.float-container {
  border: 3px solid #fff;
}

.float-child {
  width: 48%;
  float: left;
  padding-top: 10px;
}

.flex-container {
  display: flex;
  /* flex-flow: column wrap; */
  flex-shrink: 0;
  flex-wrap: wrap;
}

.flex-entry {
  flex: 1;
}

.color-popover {
  position: relative;
  z-index: 2;
}

.popover {
  position: relative;
  z-index: 2;
}

.color-cover {
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}

.color-container {
  position: absolute;
  transform: translate(-100%, -100%);
}

.main-container {
  display: flex;
  /* flex-flow: column wrap; */
  flex-shrink: 0;
  flex-wrap: wrap;
}

.perspective-view, .edit-view {
  flex: 1;
}

.edit-container {
  display: flex;
  flex-shrink: 0;
}

.fullscreen {
  position: fixed;
  left: 0px;
  right: 0px;
  bottom: 0px;
  top: 0px;
}

.transparent {
  background-color: rgba(255, 255, 255, 0.75);
}

.help-window {
  position: absolute;
  width: 75%;
  height: 75%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  border: solid 2px black;
  overflow: auto;
  padding: 10px;
  background-color: white;
}

.load-window {
  position: absolute;
  width: 50%;
  height: 50%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  border: solid 2px black;
  overflow: auto;
  padding: 10px;
  background-color: white;
}

.resize-canvas-window {
  border: solid 2px black;
  overflow: auto;
  padding: 10px;
  background-color: white;
}

.left-to {
  position: absolute;
  transform: translate(-100%, -50%);
}

.bottom-start {
  position: absolute;
  transform: translate(0%, -100%);
}

.right-to {
  position: absolute;
  transform: translate(100%, -50%);
}

.edit-entry {
  flex: 1;
}

.edit-sidebar {
  flex: 1;
  justify-content: left;
  text-align: left;
}

.edit-sidebar-entry {
  padding: 2px;
  border-bottom: 2px solid black;
}

.title, .subtitle {
  font-weight: bold;
  display: block;
  margin-bottom: 5px;
}

canvas {
  border: solid 1px black;
  margin: 5px;
}

.toolselect-buttons button {
  display: inline-block;
}

.group {
  padding: 4px;
  border: 2px solid black;
  display: inline-block;
}